  transform: none !important;
}
:host ion-item {
  --background: #fff;
  background-color: #fff;
}

ion-list {
  padding-top: 0;
}
ion-list ion-card-subtitle {
  font-size: 18px;
}

.download {
  margin-top: 20px;
}
.download a {
