  font-size: 16px;
}
.modal-text-header ion-card-subtitle ion-text {
  font-size: 14px;
  display: block;
  color: #000;
}

ion-modal.homework-modal ion-header ion-toolbar ion-title.main-title {
  color: #fff;
  --color:#fff;
  font-size: 14px;
}

.time-wrapper {
  padding: 0 15px;
  padding-top: 0px;
}
.time-wrapper .period {
  position: relative;
  display: flex;
  padding-top: 30px;
  margin-bottom: 15px;
}
.time-wrapper .period.color1 {
  background-color: #b5d6d6;
}
.time-wrapper .period.color2 {
  background-color: #ff7477;
}
.time-wrapper .period.color3 {
  background-color: aquamarine;
}
.time-wrapper .period.color4 {
  background-color: #e69597;
}
.time-wrapper .period.color5 {
  background-color: #a0cedd;
}
.time-wrapper .period.color6 {
  background-color: #b8c5d5;
}
.time-wrapper .period.color7 {
  background-color: #f7e78e;
}
.time-wrapper .period span {
  display: block;
  width: 100%;
}
.time-wrapper .period .subject {
  font-size: 13px;
  font-weight: 500;
  background-color: #d3e1ed;
  border-radius: 8px;
  padding: 12px 10px;
  margin-bottom: 0px;
}
.time-wrapper .period .class {
  font-size: 13px;
  font-weight: normal;
  width: 110px;
}
.time-wrapper .period .class span {
  display: block;
  width: 100%;
}
.time-wrapper .period .time {
  font-size: 13px;
  font-weight: normal;
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
}
.time-wrapper .period .time .left {
  flex: 1;
  white-space: nowrap;
  position: relative;
  top: 8px;
  padding: 0;
  margin-right: 7px;
  color: #868b91;
}
.time-wrapper .period .time .border-dashed {
  flex: 1 1 auto;
  border-bottom: 1px dashed #ccc;
}

.white-space {
  white-space: nowrap;
  overflow-x: scroll;
  padding: 15px 0;
  padding-top: 0;
  display: flex;
  column-gap: 4px;
}

.month ion-chip {
  display: inline-block;
  --background:#d5d8da;
  padding: 8px 2px;
  min-width: 40px;
}
.month ion-chip ion-label {
  width: 100%;
  text-align: center;
  display: block;
  font-size: 11px;
  color: #000;
}
.month ion-chip ion-label.day {
  font-weight: 500;
  color: #000 !important;
}
.month ion-chip ion-label.date {
  font-weight: 500;
  color: #525151 !important;
}
.month ion-chip.selected {
  --background: linear-gradient(135deg, #da8e63, #c754aa);
}
.month ion-chip.selected ion-label {
  color: #fff !important;
}

.fw-500 {
  font-weight: 500;
}

.date-header {
  padding-left: 15px;
}
.date-header ion-card-subtitle {
  text-transform: initial;
}
.date-header ion-card-subtitle span + span {
  font-weight: 500;
  letter-spacing: 0;
}

ion-list {
  padding-top: 0;
  background-color: #fff !important;
  padding: 6px 0;
  margin: 0 12px;
}
ion-list ion-card {
  box-shadow: 0px 0px 0px 0px rgba(204, 204, 204, 0.7098039216);
  display: flex;
  align-items: flex-start;
  border-radius: 4px;
  margin: 2px;
  background-color: transparent;
}
ion-list ion-card ion-card-header {
  padding: 5px;
  width: 135px;
}
ion-list ion-card ion-card-header ion-card-subtitle {
  color: #ff7307;
  font-size: 12px;
  text-align: center;
}
ion-list ion-card ion-card-header ion-card-subtitle.class {
  color: #110a3b;
  font-size: 15px;
  margin-top: 0;
}
ion-list ion-card ion-card-content {
