  margin-left: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  line-height: 1.4;
  font-size: 11px;
  color: #828891;
  width: 100%;
  min-height: 39px;
  display: flex;
}
ion-list ion-card ion-card-content ion-title {
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 5px;
  padding: 0;
  color: #000;
}

.no-bg {
  background-color: transparent !important;
  padding: 0;
  margin: 0 0px 10px;
}
.no-bg ion-card {
  border-bottom: 1px solid #ccc !important;
  border-radius: 0 !important;
